<template>
    <main id="paiementPart">
        <div class="cheminSite">
        <router-link to="/">Accueil</router-link>
        <span>></span>
        <router-link to="#">Paiement</router-link>
        </div>

        <div class="paiementTotal">
            <h2>Total </h2>
            <span>200 $</span>
        </div>

        <div class="paiementForm">
            <h2>Formulaire de paiement</h2>
            <form action="">
                <div class="formSegment">
                    <input type="text" placeholder="Nom" 
                    name="nom" v-model="formField.nom"
                    v-on:input="handleFieldChange($event)">
                    <div v-html="getError('nom')"></div>
                </div>
                <div class="formSegment">
                    <input type="text" placeholder="Prenom" name="prenom"
                    v-model="formField.prenom"
                    v-on:input="handleFieldChange($event)">
                    <div v-html="getError('prenom')"></div>
                </div>

                 <div class="formSegment">
                    <input type="mail" placeholder="Mail" name="mail"
                    v-model="formField.mail"
                    v-on:input="handleFieldChange($event)">
                    <div v-html="getError('mail')"></div>
                </div>
                <div class="formSegment">
                    <input type="text" placeholder="Adresse">
                </div>

                 <div class="formSegment">
                    <select name="pays" v-model="formField.pays">
                        <option selected disabled>Choisir votre pays</option>
                        <option value="Benin">Benin</option>
                        <option value="France">France</option>
                    </select>
                </div>
                <div class="formSegment">
                    <select name="ville" id="">
                        <option value="">Ville</option>
                    </select>
                </div>

                <h2>Information bancaire Carte</h2>
                <div class="formSegment">
                    <input type="text" name="identit√©Carte"
                    v-model="formField.identit√©Carte" 
                    placeholder="Nom de la carte"
                    v-on:input="handleFieldChange($event)">
                    <div v-html="getError('identit√©Carte')"></div>
                </div>
                <div class="formSegment">
                     <input type="text" name="numeroCarte" 
                     v-model="formField.numeroCarte" 
                     placeholder="Numero de carte bancaire"
                     v-on:input="handleFieldChange($event)">
                     <div v-html="getError('numeroCarte')"></div>
                </div>
                

                <div class="formBtn">
                    <button v-if="checkFormState(formField, errors)" class="formBtnActive">
                        Soumettre</button>
                    <button v-else disabled>Soumettre</button>
                </div>
            </form>
        </div>
    </main>
</template>
<script>
import '../assets/css/paiement.css';
import {isValidChar,isValidLenght} from '../Tools/formValidator';

export default ({
    data()
    {
        return {
            formField: {
                nom: '',prenom: '',mail: '',pays: '',
                'code-postal': '',identit√©Carte: '', numeroCarte: ''
            },
            errors: []
        }
    },
    name:'Paiement',
    methods: {
        handleFieldChange(event){
            let key = event.target.name;
            let value = event.target.value;
            if (['nom', 'prenom','adresse','mail'].includes(key)) {
                isValidLenght(this.errors, this.setErrors, key, value, 5, 15,'le nom,le prenom ou l\'adresse');
                if (['nom', 'prenom'].includes(key)) {
                    isValidChar('isAlpha', this.errors, this.setErrors, key, value, 'le nom ou le prenom')
                }
                if (key == 'mail') {
                    isValidChar('isMail', this.errors, this.setErrors, key, value, 'le mail')
                }
                if (key == 'adresse') {
                    isValidChar('isAlphaWithSpace', this.errors, this.setErrors, key, value, 'l\'adresse ')
                }
            }
            if(key == 'identit√©Carte') {
                isValidChar('isAlphaWithSpace', this.errors, this.setErrors, key,value, 'le nom ou le prenom de la carte')
            }
            if(key == 'numeroCarte') {
                isValidChar('isNumeric', this.errors, this.setErrors, key,value, 'le numero de la carte')
            }
        },
        setErrors(newerrors){
            this.errors = newerrors;
        },
        getError(name){
            const errorList = this.errors.filter(item => item.name == name);
            if (errorList.length > 0) {
                return errorList.map(err => '<span class="errorFieldText">'+err.error+'</span>')
            }
        },
        checkFormState(formField, errors){

        if (formField.nom != '' && formField.prenom != ''
            && formField.adresse != '' && formField.pays != ''
             && formField['identit√©Carte'] != ''
            && formField['numeroCarte'] != '') {
            return (this.errors.length == 0) ? true : false;
        }
        else {
            return false;
        }

    }
    },
    watch:{
        
    }
})
</script>